.mapbox-container .mapboxgl-popup-close-button{transition:color 200ms;font-family:"Graphik";box-sizing:content-box;color:#8696ad;font-size:18px;padding:5px 10px 10px;width:18px}.not-mobile .mapbox-container .mapboxgl-popup-close-button:hover,.not-mobile .mapbox-container .mapboxgl-popup-close-button:focus{background-color:transparent;color:#0b1221}.mapbox-container .legend{position:absolute;right:30px;bottom:30px;background:#fff;font-size:12px;font-weight:500;letter-spacing:2px;color:#535f75;box-shadow:rgba(0,25,62,0.14) 0 12px 34px;z-index:5;text-transform:uppercase;padding:20px 30px}.mapbox-container .legend>div{display:flex;align-items:center}.mapbox-container .legend img{width:24px;height:24px;position:relative;top:1px;margin-right:5px;padding:4px 0 8px;margin-right:10px}.mapbox-container .popup{color:#8696ad;line-height:1.5;font-size:20px;max-width:27vw;min-width:20vw;padding:25px}.mapbox-container .popup h3{text-transform:uppercase;margin:0;margin-bottom:25px;font-size:24px;color:#0b1221}@media only screen and (max-width: 790px){.mapbox-container .popup{font-size:14px;max-width:50vw;padding:10px}.mapbox-container .popup h3{font-size:16px}}
