.careers-alternating-columns .title-wrap{text-align:center;margin-top:60px}.careers-alternating-columns .title-wrap .title{margin:0;font-size:50px;margin-top:20px}.careers-alternating-columns .column-wrap{list-style:none;margin:40px auto 0;padding:0}@media (max-width: 800px){.careers-alternating-columns .column-wrap{padding:0 20px}}.careers-alternating-columns .column-wrap .item{display:grid;grid-template-columns:repeat(2, 1fr);grid-column-gap:80px;max-width:700px;margin:15px auto 0}@media (max-width: 800px){.careers-alternating-columns .column-wrap .item{grid-template-columns:100%;margin-top:30px}}.careers-alternating-columns .column-wrap .item .image{width:100%}.careers-alternating-columns .column-wrap .item .headline{margin:40px 0 0;color:#016bff;font-weight:300;font-size:24px}@media (max-width: 800px){.careers-alternating-columns .column-wrap .item .headline{margin-top:15px}}.careers-alternating-columns .column-wrap .item .content{margin-top:30px}@media (max-width: 800px){.careers-alternating-columns .column-wrap .item .content{margin-top:15px}}@media (min-width: 800px){.careers-alternating-columns .column-wrap .item:nth-child(even) .text-wrap{grid-column-start:1;grid-row-start:1;text-align:right}}
