.partner-program-opportunities{padding:0 30px;margin:60px auto 40px;max-width:1200px}@media (max-width: 800px){.partner-program-opportunities{max-width:500px}}.partner-program-opportunities .grid{display:grid;grid-template-columns:repeat(3, 1fr);grid-column-gap:70px;list-style:none;margin:0;padding:0;text-align:center}@media (max-width: 800px){.partner-program-opportunities .grid{grid-template-columns:100%;grid-row-gap:40px}}.partner-program-opportunities .grid .icon{width:100px;height:100px;margin:auto}.partner-program-opportunities .grid .title{font-size:40px;line-height:1.1 !important;font-weight:500;margin:25px 0 0}@media (max-width: 800px){.partner-program-opportunities .grid .title{font-size:26px}}.partner-program-opportunities .grid .content{color:#8695ae;margin-top:25px}.partner-program-opportunities .grid .end-link{text-decoration:none;padding:24px 50px 25px;font-size:16px;color:#0b1221;background:#fff;border-radius:45px;display:inline-block;font-weight:500;transition:border 0.3s;white-space:nowrap;transition:opacity 0.3s}.partner-program-opportunities .grid .end-link:not(.primary){background:#0f61ff;color:#fff;border:3px solid rgba(15,97,255,0.3);padding-bottom:23px;padding-top:22px}.not-mobile .partner-program-opportunities .grid .end-link:not(.primary):hover,.not-mobile .partner-program-opportunities .grid .end-link:not(.primary):focus{border-color:transparent}.partner-program-opportunities .grid .end-link+.shortcode-button{margin-left:20px}@media only screen and (max-width: 790px){.partner-program-opportunities .grid .end-link{font-size:14px;padding-right:35px;padding-left:35px}}.partner-program-opportunities .grid .end-link:hover,.partner-program-opportunities .grid .end-link:focus{opacity:0.7}
