.red-team-landing{color:#fff;overflow-x:hidden;background:#000}.red-team-landing .grid-background{position:absolute;bottom:0;right:0;left:0;top:0;background:url("images/patterns/triangles.png");background-size:50px;opacity:0.07;pointer-events:none}.red-team-landing .hero{position:relative;min-height:75vh;display:flex;align-items:center;background:linear-gradient(rgba(238,0,52,0.3), rgba(1,19,34,0)),#051d30}.red-team-landing .content-wrap{position:absolute;bottom:0;right:0;left:0;top:0;padding:80px 30px;display:flex;flex-direction:column;justify-content:center;margin:auto;max-width:1200px;position:relative;z-index:10;pointer-events:none}.red-team-landing .content-wrap .srt-logo{width:170px;height:auto;position:relative;top:2px;right:12px}.red-team-landing .content-wrap .wp-content{font-size:24px;width:40%}.red-team-landing .content-wrap .wp-content>*:first-child{margin-top:0}.red-team-landing .content-wrap .wp-content h3{font-size:60px;margin-bottom:25px}.red-team-landing .content-wrap .wp-content p{color:#b8c4da;line-height:1.4;margin-bottom:30px}.red-team-landing .content-wrap .wp-content a{pointer-events:all}.red-team-landing .world-map{position:absolute;right:-20px;bottom:65px;width:40%;height:auto;pointer-events:none}.red-team-landing .dots-bottom{position:absolute;right:0;bottom:0;left:0;height:50px;background:url("images/patterns/dots.png");background-size:10px;opacity:0.1}.red-team-landing .announcements{color:#8695ae;position:relative;z-index:5}.red-team-landing .announcements ul{list-style:none;margin:0 auto;padding:0;min-height:20vh;display:grid;grid-template-columns:repeat(3, 33.3333%);max-width:1350px}.red-team-landing .announcements .announcement{text-align:center;line-height:1.4;margin:auto;height:100%;display:flex;flex-direction:column;justify-content:space-between;padding:20px 0;background:#000}.red-team-landing .announcements .announcement+.announcement{border-left:2px solid #182339}.red-team-landing .announcements .announcement>*{width:70%;margin:auto}.red-team-landing .announcements .announcement .responsive-image{max-width:150px}.red-team-landing .announcements .announcement p{padding:0;margin:0}.red-team-landing .announcements .announcement a{color:#fff;text-decoration:none}.red-team-landing .announcements .announcement .link-wrap{text-transform:uppercase;font-size:14px;letter-spacing:1px;margin:30px auto 40px;height:1em}.red-team-landing .announcements .announcement .link-wrap a{padding:10px}.red-team-landing .announcements .announcement .link-wrap svg{margin-left:10px}@media only screen and (max-width: 790px){.red-team-landing{overflow:hidden}.red-team-landing *[data-parallax]{transform:none !important}.red-team-landing .hero{display:block;overflow-y:hidden}.red-team-landing .world-map{display:none}.red-team-landing .content-wrap{padding-top:80px;padding-bottom:20px;display:block;text-align:center}.red-team-landing .content-wrap .srt-logo{position:static;margin:auto;width:73px}.red-team-landing .content-wrap .wp-content{width:100%;font-size:16px}.red-team-landing .content-wrap .wp-content h3{font-size:30px}.red-team-landing .announcements ul{display:block}.red-team-landing .announcements .announcement{padding:20px 0}.red-team-landing .announcements .announcement+.announcement{border-left:none;border-top:2px solid #182339}}
