.journey-report{grid-column:2 / span 2}.journey-report .label{font-size:10px;color:#64778f;letter-spacing:3px;text-align:center;text-transform:uppercase}.journey-report .report-inner{border:1px solid #31495f;padding:30px}.journey-report .top-wrap{display:grid;grid-template-columns:repeat(3, 1fr);padding:0 30px 30px;border-bottom:1px solid #31495f}.journey-report .top-wrap .title{grid-column:1 / span 3;font-size:10px;color:#64778f;letter-spacing:3px;text-align:center;text-transform:uppercase;color:#fff;font-weight:400;font-size:14px;margin:0 auto 45px}.journey-report .top-wrap .amount{text-align:center;color:#fff;font-weight:500;font-size:40px}.journey-report .top-wrap .label{display:flex;align-items:flex-end;justify-content:center;margin-bottom:6px}.journey-report .top-wrap .amount{margin-top:6px}.journey-report .top-wrap .label.vulns{grid-area:2 / 1 / span 1 / span 1}.journey-report .top-wrap .amount.vulns{grid-area:3 / 1 / span 1 / span 1}.journey-report .top-wrap .journey-svg-score{grid-area:2 / 2 / span 2 / span 1;text-align:center;margin:auto;width:100%}.journey-report .bottom-wrap{display:grid;grid-template-columns:[breakdown] 1fr [categories] 2fr;grid-gap:40px 100px;grid-auto-flow:dense;padding:30px 30px 0}.journey-report .bottom-wrap .label.breakdown{grid-column:breakdown}.journey-report .bottom-wrap .breakdown-wrap{grid-column:breakdown}.journey-report .bottom-wrap .breakdown-wrap .breakdown-inner{display:flex}.journey-report .bottom-wrap .breakdown-wrap svg{margin:auto}.journey-report .bottom-wrap .breakdown-wrap path{stroke:#ff3655}.journey-report .bottom-wrap .breakdown-wrap path:nth-of-type(2){stroke:#48d895}.journey-report .bottom-wrap .breakdown-wrap path:nth-of-type(3){stroke:#ffad39}.journey-report .bottom-wrap .breakdown-wrap .legend-wrap{display:grid;grid-template-columns:[circles] 10px [labels] 1fr;grid-gap:15px 10px;align-content:center;margin-left:25px}.journey-report .bottom-wrap .breakdown-wrap .legend-wrap .circle{width:10px;height:10px;border-radius:50%;background:#48d895;position:relative;top:2px}.journey-report .bottom-wrap .breakdown-wrap .legend-wrap .circle.med{background:#ffad39}.journey-report .bottom-wrap .breakdown-wrap .legend-wrap .circle.high{background:#ff3655}.journey-report .bottom-wrap .breakdown-wrap .legend-wrap .label{text-align:left;font-size:13px;color:#fff;text-transform:none;letter-spacing:initial}.journey-report .bottom-wrap .label.categories{grid-column:categories}.journey-report .bottom-wrap .category-grid{grid-column:categories}.journey-report .bottom-wrap .journey-category+.journey-category{margin-top:5px}.journey-report .bottom-wrap .journey-category:nth-child(2) .fill{background:#0095d5}.journey-report .bottom-wrap .journey-category:nth-child(3) .fill{background:#1676d9}.journey-report .bottom-wrap .journey-category:nth-child(4) .fill{background:#005c97}
