.government-programs{background:linear-gradient(#061123, #0e2749);overflow:hidden;padding:0 20px 250px;position:relative}.government-programs .spaced-dots{position:absolute;bottom:0;right:0;left:0;top:0;background-image:url("images/patterns/dots-spaced.png");background-size:80px;opacity:0.1}.government-programs .program-wrap{display:flex;flex-direction:column;justify-content:center;position:relative}.government-programs .program-wrap .spacer{flex:1;position:relative}.government-programs .program-wrap .spacer.line-container{transition:transform 0.4s, opacity 0.4s;transform-origin:center top}.government-programs .program-wrap .spacer.line-container.below-view:not(.fired-once){transform:scaleY(0)}.government-programs .program-wrap .spacer .line-down{position:absolute;top:0;left:0;bottom:0;width:1px;background:linear-gradient(to bottom, rgba(53,66,86,0), #354256)}.government-programs .program-wrap .title{color:#48d895;text-transform:uppercase;font-size:18px;letter-spacing:2px;font-weight:400;margin:0}.government-programs .program-wrap .wp-content{width:50%;color:#fff}.government-programs .program-wrap .wp-content h3{font-size:60px;font-weight:700;margin:30px 0}.government-programs .program-wrap .wp-content p{color:#8694af;font-size:20px;line-height:30px}.government-programs .program-wrap .wp-content p strong{color:#fff;font-size:18px;letter-spacing:2px;text-transform:uppercase;font-weight:400}.government-programs .program-wrap .wp-content p:last-child{margin-bottom:0}.government-programs .program-wrap .pentagon-image-wrap{position:absolute;left:20%;bottom:0}.government-programs .program-wrap .pentagon-image-wrap img{opacity:0.7}.government-programs .program-wrap .pentagon-image-wrap img+img{position:absolute;bottom:0;right:0;left:0;top:0;animation:glow 5s infinite}.government-programs .wp-content.centered-copy{color:#8694af;max-width:750px;margin:auto;text-align:center;line-height:34px;font-size:24px}.government-programs .wp-content.centered-copy strong{color:#fff;font-weight:500}.government-programs .agency-grid .line-wrap{height:240px;position:relative}.government-programs .agency-grid .line-wrap.below-view:not(.fired-once) .line-down{transform:scaleY(0)}.government-programs .agency-grid .line-down{position:absolute;top:0;right:0;bottom:0;left:0;width:1px;margin:auto;background:linear-gradient(to bottom, rgba(53,66,86,0), #354256);transition:transform 0.4s 0.2s, opacity 0.4s;transform-origin:center top}.government-programs .agency-grid .line-down .bullseye{position:absolute;bottom:0;left:0;transform:translate(-8.5px, 50%)}.government-programs .agency-grid .wp-content{margin:40px auto}.government-programs .agency-grid .agencies{list-style:none;padding:0;margin:0;display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:25px}.government-programs .agency-grid .agencies.below-view:not(.fired-once) .agency{transform:translateY(10px);opacity:0}.government-programs .agency-grid .agencies .agency{width:100%;position:relative}.government-programs .agency-grid .agencies .agency:nth-child(1){transition:transform 0.4s .1s,opacity 0.4s .1s}.government-programs .agency-grid .agencies .agency:nth-child(2){transition:transform 0.4s .2s,opacity 0.4s .2s}.government-programs .agency-grid .agencies .agency:nth-child(3){transition:transform 0.4s .3s,opacity 0.4s .3s}.government-programs .agency-grid .agencies .agency:nth-child(4){transition:transform 0.4s .4s,opacity 0.4s .4s}.government-programs .agency-grid .agencies .agency:nth-child(5){transition:transform 0.4s .5s,opacity 0.4s .5s}.government-programs .agency-grid .agencies .agency:nth-child(6){transition:transform 0.4s .6s,opacity 0.4s .6s}.government-programs .agency-grid .agencies .agency:nth-child(7){transition:transform 0.4s .7s,opacity 0.4s .7s}.government-programs .agency-grid .agencies .agency:nth-child(8){transition:transform 0.4s .8s,opacity 0.4s .8s}.government-programs .agency-grid .agencies .agency:nth-child(9){transition:transform 0.4s .9s,opacity 0.4s .9s}.government-programs .agency-grid .agencies .agency:nth-child(10){transition:transform 0.4s 1s,opacity 0.4s 1s}.government-programs .agency-grid .agencies .agency:nth-child(11){transition:transform 0.4s 1.1s,opacity 0.4s 1.1s}.government-programs .agency-grid .agencies .agency:nth-child(12){transition:transform 0.4s 1.2s,opacity 0.4s 1.2s}.government-programs .agency-grid .agencies .agency img{width:100%;height:auto;margin:auto}@media only screen and (max-width: 790px){.government-programs{text-align:center;padding-bottom:120px}.government-programs .program-wrap{display:block}.government-programs .program-wrap .pentagon-image-wrap{top:0;left:0;width:140%;height:auto;transform:translate(5%, -9%)}.government-programs .program-wrap .pentagon-image-wrap img{transform:translate(-15%, -30%) !important;width:100%}.government-programs .program-wrap .title{padding:40vh 20px 0}.government-programs .program-wrap .wp-content{padding:0 20px;width:100%;box-sizing:border-box}.government-programs .program-wrap .wp-content h3{font-size:32px}.government-programs .program-wrap .wp-content+.line-container{height:220px;position:relative}.government-programs .program-wrap .wp-content+.line-container .line-down{right:0;margin:auto;height:220px}.government-programs .agency-grid .wp-content{margin-bottom:20px}.government-programs .agency-grid .agencies{padding:0 20px;grid-template-columns:repeat(2, 1fr);grid-gap:20px}}
