.red-team-platform{position:relative;background:linear-gradient(#000e1a, #041829)}.red-team-platform .main-info{position:relative;overflow:hidden;transition:transform 0.4s, opacity 0.4s}.red-team-platform .main-info.below-view:not(.fired-once){transform:translateY(50px);opacity:0}.red-team-platform .copy-wrap{margin:auto;padding:160px 30px;max-width:1100px;color:#8694af}.red-team-platform .copy-wrap .label{font-size:18px;text-transform:uppercase;opacity:0.4;font-weight:500;letter-spacing:2px;margin:0 auto 15px}.red-team-platform .copy-wrap .wp-content{max-width:40%}.red-team-platform .copy-wrap .wp-content h3{color:#fff;font-size:60px;margin:25px 0 35px}.red-team-platform .copy-wrap .wp-content h3 em{color:#ff3753;font-style:normal;display:block}.red-team-platform .copy-wrap .wp-content p{line-height:1.5;font-size:20px}.red-team-platform .copy-wrap .wp-content p em:first-of-type{font-style:normal;color:#fff;text-transform:uppercase;letter-spacing:1px}.red-team-platform .platform-slideshow{position:absolute;top:calc(160px / 2);right:0;bottom:0;width:100vw;transform:translateX(45%)}.red-team-platform .features-grid{padding:0 30px;margin:110px auto 0;list-style:none;color:#8695ae;max-width:1200px;display:grid;grid-template-columns:repeat(4, 1fr);grid-column-gap:25px}.red-team-platform .features-grid.below-view:not(.fired-once) .feature-block{transform:translateY(50px);opacity:0}.red-team-platform .features-grid .feature-block{text-align:center;padding:30px;line-height:1.4;position:relative;transition:transform 0.4s, opacity 0.4s}.red-team-platform .features-grid .feature-block:nth-child(1){transition-delay:.1s}.red-team-platform .features-grid .feature-block:nth-child(2){transition-delay:.2s}.red-team-platform .features-grid .feature-block:nth-child(3){transition-delay:.3s}.red-team-platform .features-grid .feature-block:nth-child(4){transition-delay:.4s}.red-team-platform .features-grid .feature-block:nth-child(5){transition-delay:.5s}.red-team-platform .features-grid .feature-block:nth-child(6){transition-delay:.6s}.red-team-platform .features-grid .feature-block:nth-child(7){transition-delay:.7s}.red-team-platform .features-grid .feature-block:nth-child(8){transition-delay:.8s}.red-team-platform .features-grid .feature-block .feature-logo-wrap{width:120px;height:120px;margin:10px auto 40px;background:linear-gradient(#000b15, #03192d);border-radius:50%;position:relative;display:flex;align-items:center;justify-content:center}.red-team-platform .features-grid .feature-block .feature-logo{width:40%;height:auto}.red-team-platform .features-grid .feature-block strong{display:block;color:#fff;margin-bottom:25px;font-size:20px;font-weight:500}.red-team-platform .section-levels,.red-team-platform .section-income{padding:0 30px;margin:140px auto 0;color:#8695ae;display:flex;justify-content:space-between;align-items:center;max-width:1200px}.red-team-platform .section-levels .wp-content,.red-team-platform .section-income .wp-content{width:50%;font-size:18px;line-height:28px}.red-team-platform .section-levels .wp-content h3,.red-team-platform .section-income .wp-content h3{margin:0 0 30px;font-size:40px;color:#fff;font-weight:500;line-height:1.3}.red-team-platform .section-levels .wp-content strong,.red-team-platform .section-income .wp-content strong{font-size:18px;text-transform:uppercase;color:#fff;font-weight:400;letter-spacing:1px}.red-team-platform .section-levels svg,.red-team-platform .section-income svg{width:35%;height:auto}.red-team-platform .section-levels .graphic-wrap,.red-team-platform .section-income .graphic-wrap{width:35%;position:relative}.red-team-platform .section-levels .graphic-wrap .image-wrap,.red-team-platform .section-income .graphic-wrap .image-wrap{transform-style:preserve-3d;overflow:visible}.red-team-platform .section-levels .graphic-wrap img,.red-team-platform .section-income .graphic-wrap img{width:100%;height:auto;transform:translateZ(50px)}.red-team-platform .section-levels .graphic-wrap img.blurred,.red-team-platform .section-income .graphic-wrap img.blurred{position:absolute;bottom:0;right:0;left:0;top:0;transform:scale(1.07);opacity:0.5}.red-team-platform .svg-levels-graphic{transform:translateZ(0)}.red-team-platform .section-income{transform:translateZ(0);margin-top:120px}.red-team-platform .gradient-bottom{margin-top:55px;height:80px;width:100%;background:linear-gradient(rgba(255,54,85,0), rgba(255,54,85,0.4))}@media only screen and (max-width: 790px){.red-team-platform .main-info{display:flex;flex-direction:column}.red-team-platform .main-info .platform-slideshow{position:relative;height:50vh;transform:none;width:100vw;top:0;margin-top:70px}.red-team-platform .main-info .platform-slideshow img{object-position:center center !important}.red-team-platform .main-info .copy-wrap{order:1;padding-top:70px;padding-bottom:70px;text-align:center}.red-team-platform .main-info .copy-wrap .wp-content{max-width:100%}.red-team-platform .main-info .copy-wrap .wp-content h3{font-size:40px}.red-team-platform .main-info .copy-wrap .wp-content p{line-height:1.7}.red-team-platform .features-grid{margin-top:0;padding:0 15px;display:block}.red-team-platform .features-grid .feature-block{max-width:400px;margin:auto}.red-team-platform .features-grid .feature-block+.feature-block{margin-top:20px}.red-team-platform .section-levels,.red-team-platform .section-income{flex-direction:column;margin-top:60px;text-align:center}.red-team-platform .section-levels .wp-content,.red-team-platform .section-income .wp-content{order:1;width:100%;margin-top:35px;font-size:16px}.red-team-platform .section-levels .wp-content h3,.red-team-platform .section-income .wp-content h3{font-size:30px;margin:0 0 25px}.red-team-platform .section-levels .wp-content strong,.red-team-platform .section-income .wp-content strong{font-size:14px}.red-team-platform .section-levels svg,.red-team-platform .section-levels .graphic-wrap,.red-team-platform .section-income svg,.red-team-platform .section-income .graphic-wrap{wid