.company-page-investors{padding:150px 30px 140px;position:relative}.company-page-investors .dots{background-image:url("images/patterns/dots.png");background-size:8px;position:absolute;height:40px;right:0;left:0;top:3px}.company-page-investors .gradient{position:absolute;bottom:0;right:0;left:0;top:0;background:linear-gradient(0deg, #fff 0%, #eef5ff 100%)}.company-page-investors .titles{text-align:center;margin:auto;margin-bottom:80px}.company-page-investors .titles h5{text-transform:uppercase;letter-spacing:2px;font-weight:500;font-size:18px;color:#0f61ff;margin:0}.company-page-investors .titles h3{font-size:60px;margin:30px 0}.company-page-investors .partner-grid{grid-template-columns:1fr 1fr 1fr;list-style-type:none;grid-gap:25px;display:grid;padding:0;margin:0}.company-page-investors .partner-block .wrapper{border:1px solid #dfe5f0;background-color:#fff;border-radius:10px;text-align:center;padding:60px;display:flex;flex-direction:column;align-items:center;justify-content:center;text-decoration:none;color:#000;height:100%;box-sizing:border-box}.company-page-investors .partner-block .wrapper .partner-logo{width:100%;max-width:75%;background-repeat:no-repeat;background-position:center;background-size:contain;padding-bottom:25%;filter:invert(100%)}.company-page-investors .partner-block .wrapper .description{margin-top:10px}.company-page-investors .in-view .partner-block{transition:opacity 600ms, transform 600ms;transition-delay:400ms}.company-page-investors .in-view .partner-block:nth-child(6n+1){transition-delay:475ms}.company-page-investors .in-view .partner-block:nth-child(6n+2){transition-delay:550ms}.company-page-investors .in-view .partner-block:nth-child(6n+3){transition-delay:625ms}.company-page-investors .in-view .partner-block:nth-child(6n+4){transition-delay:700ms}.company-page-investors .in-view .partner-block:nth-child(6n+5){transition-delay:775ms}.company-page-investors .in-view .partner-block:nth-child(6n+6){transition-delay:850ms}.company-page-investors .below-view .partner-block{transform:translateY(10%);opacity:0}@media only screen and (max-width: 790px){.company-page-investors{padding-right:16px;padding-left:16px;padding-bottom:60px;padding-top:70px}.company-page-investors .dots{display:none}.company-page-investors .titles{margin-bottom:40px}.company-page-investors .titles h5{font-size:18px}.company-page-investors .titles h3{font-size:28px;margin:20px 0}.company-page-investors .partner-grid{grid-template-columns:1fr 1fr;grid-gap:16px}.company-page-investors .partner-block .wrapper{padding:40px 20px}.company-page-investors .partner-block .description{display:none}.company-page-investors .partner-block .wrapper .partner-logo{margin-bottom:0}}
