.platform-slideshow img{object-position:center left !important}.platform-slideshow .current{transform:scale(1.05) translateX(5%)}.platform-slideshow .current-slide-enter{transform:scale(1) translateX(0)}.platform-slideshow .current-slide-enter-to{transform:scale(1.05) translateX(5%)}.platform-slideshow .current-slide-leave-to{transform:scale(1.1) translateX(10%);opacity:0;z-index:5}.platform-slideshow .current-slide-enter-active,.platform-slideshow .current-slide-leave-active{transition:transform 1s, opacity 1s}.platform-slideshow .current-slide-leave-active{z-index:5}@media only screen and (max-width: 790px){.platform-slideshow .responsive-image.preview{transform:translateX(-5%)}}
