.company-page-team-block{background-color:#314152;position:relative}.company-page-team-block .image .media{mix-blend-mode:luminosity}.company-page-team-block .overlay{transition:opacity 250ms;background-color:#0b1221;position:absolute;bottom:0;right:0;left:0;top:0;opacity:0.15}.company-page-team-block .meta{transition:opacity 50ms;transform:translateZ(0);position:absolute;text-align:left;color:#acb5c7;padding:20px;opacity:0;bottom:0;right:0;left:0}.company-page-team-block .team-title{margin-bottom:20px;color:#fff}.company-page-team-block .team-title .name{font-family:"criquegrotesk";text-transform:uppercase;font-weight:400;display:block;font-size:0}.company-page-team-block .team-title .name+.name{margin-top:-1px}.company-page-team-block .team-title .name span{border:1px solid #fff;display:inline-block;padding:8px 12px;font-size:14px}.company-page-team-block .team-title .name .title{display:inline-block;background-color:#fff;color:#0b1221}.not-mobile .company-page-team-block:hover .overlay,.not-mobile .company-page-team-block:focus .overlay{opacity:0.45}.company-page-team-block.is-active .overlay,.not-mobile .company-page-team-block.is-active .overlay{opacity:0.8}.company-page-team-block.is-active .meta{animation-fill-mode:forwards;animation:fadeup 550ms;animation-delay:350ms;transition-delay:350ms;opacity:1}@media only screen and (max-width: 790px){.company-page-team-block{width:100% !important}.company-page-team-block .meta{display:none}.company-page-team-block .overlay{display:none}}@keyframes fadeup{0%{transform:translateY(10%);opacity:0}100%{transform:none;opacity:1}}
