.careers-hero .hero{position:relative}.careers-hero .hero .background{width:100%;position:absolute;bottom:0;right:0;left:0;top:0;height:100%;object-fit:cover}.careers-hero .hero .gradient{position:absolute;bottom:0;right:0;left:0;top:0;background:linear-gradient(to top right, #390577, #1c48bf);mix-blend-mode:hard-light}.careers-hero .hero .content{color:#fff;display:flex;justify-content:center;flex-direction:column;padding:120px 100px 120px 15px;text-align:left;max-width:1200px;margin:auto;box-sizing:border-box;position:relative}@media (max-width: 800px){.careers-hero .hero .content{position:relative;padding-top:100px;padding-right:30px}}.careers-hero .hero .content h3{font-size:60px;font-weight:700;margin:0}@media (max-width: 800px){.careers-hero .hero .content h3{font-size:32px}}.careers-hero .hero .content h4{font-size:30px;font-weight:500;margin:20px 0}@media (max-width: 800px){.careers-hero .hero .content h4{font-size:18px}}.careers-hero .hero .content a:only-child{text-decoration:none;padding:24px 50px 25px;font-size:16px;color:#0b1221;background:#fff;border-radius:45px;display:inline-block;font-weight:500;transition:border 0.3s;white-space:nowrap;transition:opacity 0.3s}.careers-hero .hero .content a:only-child:not(.primary){background:#fff;color:#000;border:3px solid rgba(255,255,255,0.3);padding-bottom:23px;padding-top:22px}.not-mobile .careers-hero .hero .content a:only-child:not(.primary):hover,.not-mobile .careers-hero .hero .content a:only-child:not(.primary):focus{border-color:transparent}.careers-hero .hero .content a:only-child+.shortcode-button{margin-left:20px}@media only screen and (max-width: 790px){.careers-hero .hero .content a:only-child{font-size:14px;padding-right:35px;padding-left:35px}}.careers-hero .hero .content a:only-child:hover,.careers-hero .hero .content a:only-child:focus{opacity:0.7}.careers-hero .gallery-band{display:grid;width:100%;grid-template-columns:repeat(6, 1fr)}.careers-hero .gallery-band .image-wrap{position:relative;height:0;width:100%;padding-bottom:100%}.careers-hero .gallery-band .image-wrap img{position:absolute;bottom:0;right:0;left:0;top:0;width:100%;height:100%;object-fit:cover}
