.government-chart-graphic .content-wrap{position:relative;display:flex;justify-content:center;align-items:center}.government-chart-graphic .content-wrap .spacer{flex:1}.government-chart-graphic .content-wrap .left-line{background:#354256;position:absolute;top:0;left:0;bottom:50%;width:1px}.government-chart-graphic .content-wrap .horizontal-line{background:#354256;height:1px;width:70%;position:relative;transition:transform 0.4s 0.4s, opacity 0.4s 0.4s;transform-origin:center left}.government-chart-graphic .content-wrap .horizontal-line .bullseye{position:absolute;top:-9px;right:-5px}.government-chart-graphic .content-wrap .horizontal-line.below-view:not(.fired-once){transform:scaleX(0)}.government-chart-graphic .content-wrap .wp-content p:first-child{margin-top:0}.government-chart-graphic .chart-wrap{width:80%;margin:40px auto 0}.government-chart-graphic .chart-wrap img{width:100%;height:auto}@media only screen and (max-width: 790px){.government-chart-graphic .content-wrap .horizontal-line{position:static}.government-chart-graphic .content-wrap .left-line{display:none}.government-chart-graphic .content-wrap .bullseye{top:0;right:0;left:0;margin:auto;transform:translate(-2.5px, -50%)}.government-chart-graphic .content-wrap .wp-content{padding:25px 20px 0;font-size:20px}}
