.company-page-landing{-webkit-clip-path:polygon(100% 0, 110% 80%, 75% 100%, -10% 92%, 0 0);clip-path:polygon(100% 0, 110% 80%, 75% 100%, -10% 92%, 0 0);position:relative;background:#0b1221;overflow:hidden;color:#fff;background:linear-gradient(59deg, #061123 0%, #0f2748 100%);padding:200px 0}.company-page-landing .contained{display:flex}.company-page-landing .contained .col-1{width:55%}.company-page-landing .shadow{transform-origin:bottom left;transform:rotate(-25deg);position:absolute;height:40px;height:200vh;width:200vw;bottom:0;left:0;box-shadow:0px 0px 50px #000}.company-page-landing .shadow-2{bottom:-400px}.company-page-landing .pattern{background-image:url("images/patterns/triangles.png");background-size:50px;opacity:0.07;position:absolute;bottom:0;right:0;left:0;top:0;bottom:-100vh}.company-page-landing .info{padding:0 30px;position:relative;text-align:center;max-width:800px;margin:auto}.company-page-landing .title{text-transform:uppercase}.company-page-landing .title h2{font-size:160px;line-height:1;margin:0}.company-page-landing .title .line{white-space:nowrap;display:block}.company-page-landing .title .line-2{margin-top:-0.43em;color:rgba(255,255,255,0.7);text-shadow:0 6px 24px rgba(11,18,33,0.6)}.company-page-landing .entry{padding:0 50px;line-height:1.4;font-size:20px;color:#8696ad}.company-page-landing .entry strong{text-transform:uppercase;font-weight:400;color:#fff}.company-page-landing .load-fade-enter-active,.company-page-landing .load-fade-leave-active{transition:opacity 350ms}.company-page-landing .load-fade-enter,.company-page-landing .load-fade-leave-to{opacity:0}@media only screen and (max-width: 790px){.company-page-landing{-webkit-clip-path:polygon(100% 0, 110% 90%, 65% 100%, -10% 100%, 0 0);clip-path:polygon(100% 0, 110% 90%, 65% 100%, -10% 100%, 0 0);padding:90px 0 100px}.company-page-landing .contained{display:block}.company-page-landing .contained .col-1{width:100%}.company-page-landing .shadow-2{left:15vw}.company-page-landing .title h2{font-size:52px;text-align:center}.company-page-landing .title .line-2{margin-top:-0.4em}}
