.products-pillars-graphic{padding-right:30px;padding-left:30px;padding-bottom:600px;padding-top:140px;position:relative;overflow:hidden;color:#fff}.products-pillars-graphic .contained{align-items:flex-start;display:flex}.products-pillars-graphic .contained .graphic,.products-pillars-graphic .contained .information{width:50%}.products-pillars-graphic .graphic{padding-right:100px;min-height:90vh}.products-pillars-graphic .titles h5{text-transform:uppercase;letter-spacing:2px;font-weight:500;margin:0;color:#e70040;font-size:18px}.products-pillars-graphic .titles h3{line-height:1;font-size:60px;margin:20px 0 40px}.products-pillars-graphic .information{padding-left:30px}.products-pillars-graphic .information .entry{color:#e0dbff;line-height:1.5;font-size:20px}.products-pillars-graphic .information .entry sup{font-size:8px}.products-pillars-graphic .information .entry strong{color:#fff}.products-pillars-graphic .product-pillar-feature{margin-bottom:30px;display:flex}.products-pillars-graphic .product-pillar-feature svg{position:relative;top:5px}.products-pillars-graphic .product-pillar-feature .feature-description{margin-left:16px;flex-shrink:2}.products-pillars-graphic .product-pillar-feature .title{font-weight:500;font-size:18px;color:#fff}.products-pillars-graphic .product-pillar-feature .content{font-size:14px}.products-pillars-graphic .product-pillar-feature .content>*:first-child{margin-top:0}.products-pillars-graphic .product-pillar-feature .content>*:last-child{margin-bottom:0}.products-pillars-graphic .pillars-controls{grid-template-columns:1fr 1fr;display:flex}.products-pillars-graphic .pillars-controls .btn-pillar{font-family:"criquegrotesk";position:relative;font-weight:500;font-size:18px;padding:40px 0;margin:0 4px;color:#fff;flex:1}.products-pillars-graphic .pillars-controls .btn-pillar .fillchange{transition:fill 250ms ease}.products-pillars-graphic .pillars-controls .btn-pillar .mask{transition:background-color 150ms ease, border-color 250ms ease;position:absolute;bottom:0;right:0;left:0;top:0;border-top-right-radius:10px;border-top-left-radius:10px;background-color:rgba(16,58,104,0.15);border:1px solid #103a68;border-bottom:none;-webkit-mask-image:linear-gradient(180deg, #000 10%, rgba(0,0,0,0) 100%);mask-image:linear-gradient(180deg, #000 10%, rgba(0,0,0,0) 100%)}.not-mobile .products-pillars-graphic .pillars-controls .btn-pillar:not(.active):hover .mask,.not-mobile .products-pillars-graphic .pillars-controls .btn-pillar:not(.active):focus .mask{background-color:rgba(16,58,104,0.3)}.products-pillars-graphic .pillars-controls .btn-pillar:first-child{margin-left:0}.products-pillars-graphic .pillars-controls .btn-pillar:last-child{margin-right:0}.products-pillars-graphic .pillars-controls .btn-pillar span,.products-pillars-graphic .pillars-controls .btn-pillar svg{position:relative;vertical-align:top}.products-pillars-graphic .pillars-controls .btn-pillar span{margin-left:10px;position:relative;top:2px}.products-pillars-graphic .pillars-controls .btn-pillar.hydra span{top:6px}.products-pillars-graphic .pillars-controls .btn-pillar.hydra.active .mask{background-color:rgba(231,0,64,0.35);border-color:#e70040}.products-pillars-graphic .pillars-controls .btn-pillar.hydra.active .fillchange{fill:#e70040}.products-pillars-graphic .pillars-controls .btn-pillar.apollo.active .mask{background-color:rgba(15,97,255,0.35);border-color:#0f61ff}.products-pillars-graphic .pillars-controls .btn-pillar.apollo.active .fillchange{fill:#0f61ff}.products-pillars-graphic .pillars-controls .btn-pillar.launchpoint.active .mask{background-color:rgba(153,239,255,0.15);border-color:#99efff}.products-pillars-graphic .pillars-controls .btn-pillar.launchpoint.active .ship{fill:#0ff8e4}.products-pillars-graphic .pillars-controls .btn-pillar.launchpoint.active .exhaust{fill:#108393}.products-pillars-graphic .gradient{position:absolute;bottom:0;right:0;left:0;top:0;background:linear-gradient(0deg, #1b125a 50%, #0d032b 100%);-webkit-clip-path:polygon(0 0, 100% 0, 100% 90%, 0 100%);clip-path:polygon(0 0, 100% 0, 100% 90%, 0 100%)}@media only screen and (max-width: 790px){.products-pillars-graphic{padding-right:16px;padding-left:16px;padding-bottom:120px;padding-top:70px}.products-pillars-graphic .dotted{display:none}.products-pillars-graphic .titles{text-align:center}.products-pillars-graphic .titles h5{font-size:18px}.products-pillars-graphic .titles h3{font-size:28px;margin:30px 0}.products-pillars-graphic .contained{display:block}.products-pillars-graphic .contained .graphic{padding-right:0;margin:auto;width:60%;min-height:initial}.products-pillars-graphic .contained .information{padding:0;width:100%}.products-pillars-graphic .pillars-controls{margin-top:30px}.products-pillars-graphic .pillars-controls .btn-pillar{padding-bottom:20px;padding-top:20px;font-size:20px;margin:0 7px}.products-pillars-graphic .pillars-controls .btn-pillar svg{width:20px}.products-pillars-graphic .pillars-controls .btn-pillar span{margin-left:3px}.products-pillars-graphic .gradient{-webkit-clip-path:polygon(0 0, 100% 0, 100% calc(100% - 30px), 0 100%);clip-path:polygon(0 0, 100% 0, 100% calc(100% - 30px), 0 100%)}}
