.journey-section{position:relative;transition:background 0.4s 1.1s;background:linear-gradient(to top, #eef5ff, #fff);overflow:hidden;height:60vw;max-height:1080px;min-height:750px}.journey-section .dotted{opacity:0.8}.journey-section .dark-background{position:absolute;bottom:0;right:0;left:0;top:0;background:#001e3e;opacity:0}.journey-section:not(.is-landing) .dark-background{opacity:1}.journey-section .landing-content{margin:0 auto 0;max-width:740px;padding:60px 40px 0;text-align:center;position:relative;z-index:5}.journey-section .landing-content .header-badge{margin:0 auto 10px;display:block;width:100px;height:auto}.journey-section .landing-content .wp-content h2{font-size:60px;line-height:1.1;margin:0 auto 20px}.journey-section .landing-content .wp-content p{color:#3e4d66;line-height:1.5;margin-top:0;font-size:20px}.journey-section .landing-content .journey-accented-button{margin-top:20px}.journey-section .journey-map-wrapper{position:absolute;right:0;bottom:0;left:0;transition:transform 1.3s}.journey-section.is-report .journey-map-wrapper{transform:translateY(40vh)}
