.company-page-careers{padding:100px 30px;background-color:#0f61ff;color:#fff}.company-page-careers .logo-debossed{display:block;margin:auto;margin-bottom:65px}.company-page-careers .entry{text-align:center;max-width:550px;font-size:20px;margin:auto}.company-page-careers .entry h3{font-size:60px;margin:30px auto}.company-page-careers .entry p{margin:30px auto}.company-page-careers .entry .shortcode-button{padding-bottom:23px;padding-top:23px;font-weight:500;font-size:14px}@media only screen and (max-width: 790px){.company-page-careers{padding-bottom:60px;padding-top:60px;padding-right:16px;padding-left:16px}.company-page-careers .logo-debossed{margin-bottom:50px}.company-page-careers .entry h3{font-size:28px}.company-page-careers .entry .shortcode-button{box-sizing:border-box;width:100%}}
