.products-grid-section .toggle{display:flex;justify-content:center;align-items:center;font-weight:500;text-align:center;font-size:20px;width:100%;padding:20px 0;background:#f9fafd url(/wp-content/themes/synack/next/static/images/toggle.png);background-position:calc(100% - 10px) center;background-repeat:no-repeat;background-size:8px 15px;transition:background 0.3s;font-family:"criquegrotesk"}.products-grid-section .toggle:active{color:inherit}.not-mobile .products-grid-section .toggle:hover,.not-mobile .products-grid-section .toggle:focus{background:#fff url(/wp-content/themes/synack/next/static/images/toggle.png);background-position:calc(100% - 10px) center;background-repeat:no-repeat;background-size:8px 15px}.products-grid-section:not(.opened) .toggle{border-bottom:1px solid #e8ebf0}.products-grid-section .table{display:grid;grid-row-gap:1px}
