.company-page-team{position:relative;padding:140px 0}.company-page-team .contained{padding-right:30px;padding-left:30px}.company-page-team .lines{position:absolute;bottom:0;right:0;left:0;top:0;margin:auto}.company-page-team .lines.in-view .line{transition:bottom 4s cubic-bezier(0.455, 0.03, 0.515, 0.955);bottom:0}.company-page-team .lines.in-view .line:nth-child(2){transition-delay:1000ms}.company-page-team .lines.in-view .line:nth-child(3){transition-delay:450ms}.company-page-team .lines .line{background-color:#e0e2e4;position:absolute;content:'';width:1px;bottom:100%;top:0}.company-page-team .lines .line:nth-child(1){transition-delay:200ms;left:30px}.company-page-team .lines .line:nth-child(2){transition-delay:1000ms;left:50%}.company-page-team .lines .line:nth-child(3){transition-delay:650ms;right:30px}.company-page-team .titles{margin-bottom:120px;text-align:center}.company-page-team .titles h5{text-transform:uppercase;font-weight:500;font-size:18px;margin:30px 0;color:#0f61ff}.company-page-team .titles h3{font-size:60px;margin:0}@media only screen and (max-width: 790px){.company-page-team{padding-bottom:60px;padding-top:70px}.company-page-team .contained{padding-right:16px;padding-left:16px}.company-page-team .lines .line:nth-child(1){left:16px}.company-page-team .lines .line:nth-child(3){right:16px}.company-page-team .titles{margin-bottom:40px}.company-page-team .titles h5{font-size:18px;margin:20px 0}.company-page-team .titles h3{font-size:32px}}
