.journey-feed{padding:0;margin:0;list-style:none;line-height:1.4;font-family:'Roboto Mono', monospace;font-size:14px}.journey-feed .item{display:grid;grid-template-columns:80px 1fr;grid-gap:10px}.journey-feed .item+.item{margin-top:20px}.journey-feed .item .speaker{text-align:right}.journey-feed .item:last-child{padding-bottom:10px}.journey-feed .normal{color:#8795ae}.journey-feed .positive{color:#b7fff8}.journey-feed .negative{color:#ff8a9c}
