.info-box{position:absolute;background:#061123;color:#535f75;width:200px;padding:20px;transform:translate(calc(-100vw + 10%), -50%);z-index:50}.info-box .title{margin-top:0;margin-bottom:10px;font-size:12px;color:#fff;text-transform:uppercase;letter-spacing:1px}.info-box .wp-content{font-size:12px;line-height:1.5}.info-box .wp-content>*:first-child{margin-top:0}.info-box .wp-content>*:last-child{margin-bottom:0}.info-box.slide-up-enter,.info-box.slide-up-leave-to{opacity:0;transform:translate(calc(-100vw + 10%), -40%)}.info-box.slide-up-enter-active,.info-box.slide-up-leave-active{transition-duration:0.25s}@media only screen and (max-width: 790px){.info-box{transform:translate(-50%, -110%)}.info-box.slide-up-enter,.info-box.slide-up-leave-to{opacity:0;transform:translate(-50%, -40%)}}
