.products-solution-indicator{position:relative;text-align:center}.products-solution-indicator .circle{transition:filter 350ms;background-repeat:no-repeat;background-position:center;background-size:contain;filter:grayscale(100%);border-radius:100%;margin:0 auto;margin-bottom:15px;height:32px;width:32px}.not-mobile .products-solution-indicator:not(.active):hover .circle,.not-mobile .products-solution-indicator:not(.active):focus .circle{filter:grayscale(75%)}.products-solution-indicator.active .circle{filter:grayscale(0%)}.products-solution-indicator .step-label{transition:color 350ms;font-family:"criquegrotesk";text-transform:uppercase;letter-spacing:2px;font-weight:500;font-size:14px;color:#8695ae}.products-solution-indicator .down-caret{position:absolute;margin-left:-7px;display:block;bottom:-37px;font-size:0;left:50%}.not-mobile .products-solution-indicator.active .step-label,.not-mobile .products-solution-indicator:hover .step-label{color:#535f75}.products-solution-indicator .arrow-enter-active,.products-solution-indicator .arrow-leave-active{transition:transform 150ms cubic-bezier(0.455, 0.03, 0.515, 0.955),clip-path 150ms cubic-bezier(0.455, 0.03, 0.515, 0.955)}.products-solution-indicator .arrow-enter,.products-solution-indicator .arrow-leave-to{-webkit-clip-path:polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);clip-path:polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);transform:translateY(-100%)}.products-solution-indicator .arrow-enter-to,.products-solution-indicator .arrow-leave{-webkit-clip-path:polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);clip-path:polygon(0 0, 100% 0, 100% 100%, 0% 100%)}@media only screen and (max-width: 790px){.products-solution-indicator{align-items:center;display:flex}.products-solution-indicator .circle{margin-right:8px;margin-bottom:0;margin-left:0}.products-solution-indicator .down-caret{display:none !important}}
