.journey-accented-button{position:relative;display:inline-block;margin:40px auto 15px}.journey-accented-button .wrap{position:relative}.journey-accented-button .decoration{border-radius:30px;border:1px solid #0f61ff;position:absolute;bottom:0;right:0;left:0;top:0;animation:highlight-button 1.8s infinite linear}.journey-accented-button .decoration:nth-child(2){animation-delay:0.6s}.journey-accented-button .decoration:nth-child(3){animation-delay:1.2s}.journey-accented-button .central-button{background:#0f61ff;color:#fff;border-radius:30px;padding:20px 30px;display:flex;align-items:center;justify-content:center;margin:0;position:relative}.journey-accented-button .central-button svg{margin-right:15px}@keyframes highlight-button{0%{transform:scale(1);opacity:1}100%{transform:scale(1.15, 1.5);opacity:0}}
