body.page-template-template-page-layout #app{background-color:#0b1221;min-height:100vh}.view-page-layout .landing-section{-webkit-clip-path:polygon(100% 0, 110% 80%, 60% 100%, 0% 100%, 0 0);clip-path:polygon(100% 0, 110% 80%, 60% 100%, 0% 100%, 0 0);padding-bottom:130px;z-index:5}.view-page-layout .landing-section .title{font-size:15vmin}@media only screen and (min-height: 700px){.view-page-layout .landing-section .title{font-size:105px}}.view-page-layout .landing-section .info{max-width:975px;margin:auto}.view-page-layout .landing-section .info .wp-content>*:first-child{margin-top:0}@media only screen and (max-width: 790px){.view-page-layout{overflow:hidden}.view-page-layout .landing-section{-webkit-clip-path:polygon(100% 0, 110% 97%, 55% 100%, -10% 96%, 0 0);clip-path:polygon(100% 0, 110% 97%, 55% 100%, -10% 96%, 0 0);padding-bottom:70px}.view-page-layout .landing-section .title{font-size:60px}}
