.journey-svg-surface-accent{transition:opacity 0.4s}.journey-svg-surface-accent:not(.accent){opacity:0}.journey-svg-surface-accent .st1{fill:#ff3655}.journey-svg-surface-accent path{transform-origin:79px 90.5px;transform:scale(0.4);animation:surface-accent 2s infinite}.journey-svg-surface-accent path+path{animation-delay:0.2s}.journey-svg-surface-accent path+path+path{animation-delay:0.4s}@keyframes surface-accent{0%{transform:scale(0.4);opacity:1}100%{transform:scale(1);opacity:0}}
