.front-page-results{color:#fff;text-align:center}.front-page-results .wp-content{display:flex;flex-direction:column;justify-content:center;padding:20px 20px;box-sizing:border-box}.front-page-results .top-wrap{background:linear-gradient(to bottom, #fff, #edf4ff);min-height:60vh;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:20px;position:relative}.front-page-results .top-wrap .triangles{position:absolute;bottom:0;right:0;left:0;top:0;top:120px;background-image:url("images/patterns/blue-triangles.png");background-size:100px}.front-page-results .top-wrap .gradient{position:absolute;bottom:0;right:0;left:0;top:0;top:120px;background:linear-gradient(180deg, #fff 10%, rgba(255,255,255,0) 100%)}.front-page-results .top-wrap .top-logo{width:220px;height:auto;margin:25px}.front-page-results .top-wrap .main-results h3,.front-page-results .top-wrap .main-results p{max-width:760px;margin:0 auto}.front-page-results .top-wrap .main-results h3{font-size:48px}.front-page-results .top-wrap .main-results p{font-size:20px;margin-top:30px;line-height:1.3}.front-page-results .bottom-wrap{display:flex}.front-page-results .bottom-wrap .wp-content{padding-top:70px;padding-bottom:70px}.front-page-results .bottom-wrap h3,.front-page-results .bottom-wrap p{max-width:400px;margin:0 auto}.front-page-results .bottom-wrap h3{font-size:40px;font-weight:500}.front-page-results .bottom-wrap p{margin-top:20px;line-height:1.3}.front-page-results .bottom-wrap .shortcode-button{margin:30px auto 0}.front-page-results .industries{background:#0c62ed;width:50%}.front-page-results .knowledge{background:#085be2;width:50%}@media only screen and (max-width: 790px){.front-page-results .top-wrap{padding:20px 16px}.front-page-results .bottom-wrap{flex-direction:column}.front-page-results .bottom-wrap .wp-content{width:100%;padding:70px 16px 60px}.front-page-results .bottom-wrap h3{font-size:30px}.front-page-results .bottom-wrap .shortcode-button{box-sizing:border-box;width:100%}}
