.partner-child-two-column{padding:110px 30px 55px;display:grid;grid-template-columns:1fr 1fr;grid-column-gap:200px;color:#8694af;max-width:1200px;margin:auto}.partner-child-two-column .shortcode-button.primary{background-color:#0f61ff;color:#fff}.partner-child-two-column .centered-column{text-align:center;grid-column:span 2;margin-bottom:30px}.partner-child-two-column .centered-column .centered-column-content{max-width:860px;margin-left:auto;margin-right:auto}@media (max-width: 950px){.partner-child-two-column .centered-column{grid-column:initial}}@media (max-width: 950px){.partner-child-two-column{grid-column-gap:100px}}@media (max-width: 800px){.partner-child-two-column{grid-template-columns:100%}}.partner-child-two-column h3{text-transform:uppercase;letter-spacing:2px;font-weight:500;color:#0f61ff;font-size:18px}.partner-child-two-column h4{font-size:60px;margin:0;color:#000}.partner-child-two-column a{text-decoration:none;padding:24px 50px 25px;font-size:16px;color:#0b1221;background:#fff;border-radius:45px;display:inline-block;font-weight:500;transition:border 0.3s;white-space:nowrap;transition:opacity 0.3s}.partner-child-two-column a:not(.primary){background:#0f61ff;color:#fff;border:3px solid rgba(15,97,255,0.3);padding-bottom:23px;padding-top:22px}.not-mobile .partner-child-two-column a:not(.primary):hover,.not-mobile .partner-child-two-column a:not(.primary):focus{border-color:transparent}.partner-child-two-column a+.shortcode-button{margin-left:20px}@media only screen and (max-width: 790px){.partner-child-two-column a{font-size:14px;padding-right:35px;padding-left:35px}}.partner-child-two-column a:hover,.partner-child-two-column a:focus{opacity:0.7}.partner-child-two-column b{color:#000}.partner-child-two-column p{margin:25px 0}.partner-child-two-column img{display:block;margin-top:25px}@media (max-width: 800px){.partner-child-two-column img{margin-right:auto;margin-left:auto}}.partner-child-two-column ul{list-style:none;padding:0}@media (max-width: 800px){.partner-child-two-column ul{padding-left:40px}}.partner-child-two-column ul li{position:relative}.partner-child-two-column ul li:before{content:'';background-image:url(/wp-content/themes/synack/next/static/images/checkbox.png);background-size:contain;width:24px;height:24px;display:block;position:absolute;right:calc(100% + 15px)}
