body{font-family:"Graphik";margin:0;-webkit-font-smoothing:antialiased !important;text-rendering:optimizeLegibility !important}::-webkit-scrollbar{display:none}h1,h2,h3,h4,h5,h6{font-family:"criquegrotesk"}button{-webkit-appearance:none;appearance:none;border:none;background-color:transparent;cursor:pointer;outline:none;margin:0;font-family:"Graphik";font-size:inherit;padding:0}.contained{max-width:1200px;margin:auto}.mobile-only{display:none}@media only screen and (max-width: 790px){.mobile-only{display:initial}.desktop-only{display:none}}.shortcode-button{text-decoration:none;padding:24px 50px 25px;font-size:16px;color:#0b1221;background:#fff;border-radius:45px;display:inline-block;font-weight:500;transition:border 0.3s;white-space:nowrap}.shortcode-button:not(.primary){background:rgba(0,0,0,0);color:#fff;border:3px solid rgba(255,255,255,0.3);padding-bottom:23px;padding-top:22px}.not-mobile .shortcode-button:not(.primary):hover,.not-mobile .shortcode-button:not(.primary):focus{border-color:transparent}.shortcode-button+.shortcode-button{margin-left:20px}@media only screen and (max-width: 790px){.shortcode-button{font-size:14px;padding-right:35px;padding-left:35px}}.wiv.in-view{transition:opacity 700ms,transform 700ms cubic-bezier(0.25, 0.46, 0.45, 0.94);transition-delay:200ms}.wiv.below-view{transform:translateY(10%);opacity:0}.fade-enter,.fade-leave-to{opacity:0}.fade-enter-active,.fade-leave-active{transition:opacity 0.4s !important}header{z-index:50}@keyframes glow{0%{opacity:0.5}50%{opacity:0}100%{opacity:0.5}}@keyframes easy-fade{0%{opacity:0}100%{opacity:1}}button,button:focus{outline:none}
