.government-other-programs{background:#edf4ff;background-image:url("images/patterns/dots.png");background-size:10px;padding-bottom:45px}.government-other-programs .program-wrap{padding:0;list-style:none;transform:translateY(-70px)}.government-other-programs .program-wrap .program{transition:transform 0.8s, opacity 0.8s}.government-other-programs .program-wrap .program.below-view:not(.fired-once){transform:translateY(100px);opacity:0}.government-other-programs .program-wrap .program+.program{margin-top:40px}.government-other-programs .program-wrap .program .wrapper{padding:180px 100px;background:#fff;position:relative;width:90%;min-width:750px;margin:0 auto;box-shadow:0 0 30px rgba(0,92,237,0.1);--slide-highlight-color: #0b1221;display:block;text-decoration:none;box-sizing:border-box;overflow:hidden}.government-other-programs .program-wrap .program .wrapper .responsive-image{-webkit-clip-path:polygon(100.1% 80%, 40% -.1%, 100.1% -1%);clip-path:polygon(100.1% 80%, 40% -.1%, 100.1% -1%)}.government-other-programs .program-wrap .program .wrapper .svg-arrow-up-right{position:absolute;top:30px;right:30px}.government-other-programs .program-wrap .program .wrapper .wp-content{width:75%;line-height:1.6;color:#8694af}.government-other-programs .program-wrap .program .wrapper .wp-content>*:last-child{margin-bottom:0}.government-other-programs .program-wrap .program .wrapper .title{font-weight:700;font-size:40px;line-height:1;color:#0b1221;margin:0;width:58%}.government-other-programs .program-wrap .program .wrapper .subtitle{font-family:"Graphik";font-weight:400;font-size:24px;margin:15px 0;color:#0b1221;color:var(--slide-highlight-color);width:58%}.not-mobile .government-other-programs .program-wrap a.wrapper:hover .responsive-image,.not-mobile .government-other-programs .program-wrap a.wrapper:focus .responsive-image{filter:url(#shared-glitch-filter)}@media only screen and (max-width: 790px){.government-other-programs .program-wrap{padding:0 20px}.government-other-programs .program-wrap .program .wrapper{padding:50px 30px 100px;min-width:initial;width:100%}.government-other-programs .program-wrap .program .wrapper .responsive-image{-webkit-clip-path:polygon(0% 100%, 100% 75%, 100% 100%);clip-path:polygon(0% 100%, 100% 75%, 100% 100%)}.government-other-programs .program-wrap .program .wrapper .svg-arrow-up-right{position:absolute;top:initial;bottom:30px}.government-other-programs .program-wrap .program .wrapper .title{font-size:26px;width:100%}.government-other-programs .program-wrap .program .wrapper .subtitle{font-size:20px;width:100%;line-height:1.4}.government-other-programs .program-wrap .program .wrapper .wp-content{width:100%}}
