.resource-landing{display:flex;align-items:center;position:relative}.resource-landing .text-wrap{box-shadow:rgba(0,25,62,0.14) 0 12px 34px;padding:30px;background:#fff;width:60%;margin:50px 0;z-index:5}.resource-landing .text-wrap .title{margin:0 0 25px;font-size:48px}.resource-landing .image-wrap{position:absolute;top:0;right:0;height:100%;width:40%}.resource-landing .clip-button{background:#106bff;padding:25px 30px;border-radius:40px;font-size:16px;white-space:nowrap;text-decoration:none;color:#fff;display:inline-block}.resource-landing .clip-button svg{position:relative;top:1px;margin-right:5px}.resource-landing .clip-button svg *{stroke:#fff;transition:0.3s}.resource-landing .clip-button .text{font-weight:400}.not-mobile .resource-landing .clip-button:hover svg *,.not-mobile .resource-landing .clip-button:focus svg *{stroke:#0f61ff}@media only screen and (max-width: 790px){.resource-landing{flex-direction:column;width:100%}.resource-landing .text-wrap{width:100%;order:1;margin:-30px 0 0;box-sizing:border-box}.resource-landing .text-wrap .title{font-size:30px}.resource-landing .image-wrap{position:relative;width:100%;padding-bottom:100%}.resource-landing .clip-button{width:100%;box-sizing:border-box;text-align:center}}
