.archive-post-block{display:flex;flex-direction:column;box-shadow:rgba(0,25,62,0.14) 0 12px 34px;color:#000;text-decoration:none;position:relative;z-index:5}.archive-post-block .block-image{height:40%;position:relative}.archive-post-block .block-image img{filter:grayscale(95%)}.archive-post-block .content-wrap{background:#fff;padding:30px 30px 50px;flex:1;display:grid;grid-template-rows:[headline] 1fr [author] auto [date] auto}.archive-post-block .title{margin:0 0 50px;font-size:24px}.archive-post-block .author{color:#0f61ff;font-size:16px;margin-bottom:10px;white-space:nowrap;width:100%}.archive-post-block .date-wrap{color:#d0d3d8;font-size:14px;text-transform:uppercase;font-weight:500;letter-spacing:3px}.archive-post-block.featured{display:block}.archive-post-block.featured .block-image{height:180px}.archive-post-block.featured .content-wrap{height:260px;box-sizing:border-box;padding:30px 30px 30px}.archive-post-block.featured .title{line-height:1.4;width:100%}.archive-post-block:not(.featured){background:transparent;box-shadow:transparent 0 12px 34px;transition:background 0.3s, box-shadow 0.3s;flex-direction:row;position:relative}.archive-post-block:not(.featured) .block-image{position:absolute;top:0;bottom:0;left:0;width:40%;height:100%}.archive-post-block:not(.featured) .content-wrap{margin-left:calc(40% + 50px);padding:35px 30px 35px 0}.archive-post-block:not(.featured) .title{margin-bottom:10px}.not-mobile .archive-post-block:not(.featured):hover,.not-mobile .archive-post-block:not(.featured):focus{box-shadow:rgba(0,25,62,0.14) 0 12px 34px;background:#fff}@media only screen and (max-width: 790px){.archive-post-block{box-shadow:none}.archive-post-block .content-wrap{padding:10px 0}.archive-post-block .title{margin-bottom:15px}.archive-post-block .date-wrap{padding-bottom:10px}.archive-post-block:not(.featured){display:block}.archive-post-block:not(.featured) .block-image{position:relative;width:100%;padding-bottom:45%}.archive-post-block:not(.featured) .content-wrap{width:100%;margin-left:0;padding:10px}.archive-post-block:not(.featured) .title{font-size:20px;line-height:1.5;margin-bottom:10px}.archive-post-block:not(.featured)+.archive-post-block{margin-top:0}.archive-post-block.featured{height:initial}.archive-post-block.featured .block-image{position:relative;width:100%;padding-bottom:45%;height:0;min-height:0}.archive-post-block.featured .content-wrap{height:initial}}
