body.page-template-template-careers #app{background-color:#0b1221;min-height:100vh}body.page-template-template-careers #app .openings-wrap #content{display:none}@media only screen and (max-width: 790px){main.view-careers .company-page-initiatives .initiatives-carousel .images{top:0;height:initial}}main.view-careers .carousel-placeholder,main.view-careers .company-page-slide{height:300px;width:500px;min-width:500px}@media only screen and (max-width: 790px){main.view-careers .company-page-initiatives .initiatives-carousel .company-page-slide{padding-bottom:56.25%}}.view-products{background:linear-gradient(to bottom, #fff, #edf4ff)}.view-products .fade-enter-active,.view-products .fade-leave-active{transition:opacity 150ms}.view-products .fade-enter,.view-products .fade-leave-to{opacity:0}
