.solutions-vector{padding-bottom:107%;position:relative;height:0}.solutions-vector .button{cursor:pointer;fill:transparent}.solutions-vector>svg{position:absolute;height:100%;width:100%}.solutions-vector image{transition:opacity 450ms ease, transform 300ms ease}.solutions-vector .activestate{opacity:0}.solutions-vector.hover-0 .synackos,.solutions-vector.hover-1 .disclose,.solutions-vector.hover-2 .smartscan,.solutions-vector.hover-3 .discover,.solutions-vector.hover-4 .certify,.solutions-vector.hover-5 .defend{transform:translateY(-5px)}.solutions-vector.hover-0 .synackos.activestate,.solutions-vector.hover-1 .disclose.activestate,.solutions-vector.hover-2 .smartscan.activestate,.solutions-vector.hover-3 .discover.activestate,.solutions-vector.hover-4 .certify.activestate,.solutions-vector.hover-5 .defend.activestate{transform:translateY(-5px);opacity:0.08}.solutions-vector.active-0 .synackos.activestate,.solutions-vector.active-1 .disclose.activestate,.solutions-vector.active-2 .smartscan.activestate,.solutions-vector.active-3 .discover.activestate,.solutions-vector.active-4 .certify.activestate,.solutions-vector.active-5 .defend.activestate{opacity:1}
