.archive-category-list ul{list-style:none;margin:0;padding:0}.archive-category-list ul a{color:#061023;text-decoration:none;text-transform:uppercase;transition:color 0.3s;padding:5px 0;display:block;letter-spacing:1px;line-height:1.3}.not-mobile .archive-category-list ul a:hover,.not-mobile .archive-category-list ul a:focus,.archive-category-list ul a.current-category{color:#ff3655}
