.archive-featured{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:20px;grid-auto-flow:dense}.archive-featured .archive-main-featured{grid-column:1 / span 3}.archive-featured .archive-post-block:nth-child(4){grid-column:2 / span 2}@media only screen and (max-width: 790px){.archive-featured{grid-template-columns:1fr}.archive-featured .archive-main-featured,.archive-featured>.archive-post-block,.archive-featured>.archive-post-block:nth-child(4){grid-column:1 / span 1}}
